* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  position: relative;
  height: 100%;
  /*background: linear-gradient(30deg, red, blue);*/
  /*background: linear-gradient(360deg, #fb7070, #005ec5, #004da0);*/
  background: -webkit-gradient(linear, left top, left bottom, from(#004da0), color-stop(#00b9c5), color-stop(#5F4B8B), color-stop(#d13171), color-stop(#fb7070), color-stop(#fdb95e), color-stop(#ffdf30), to(#3cff8d));
  background: -webkit-linear-gradient(#004da0, #00b9c5, #5F4B8B, #d13171, #fb7070, #fdb95e, #ffdf30, #3cff8d);
  background: -o-linear-gradient(#004da0, #00b9c5, #5F4B8B, #d13171, #fb7070, #fdb95e, #ffdf30, #3cff8d);
  background: linear-gradient(#004da0, #00b9c5, #5F4B8B, #d13171, #fb7070, #fdb95e, #ffdf30, #3cff8d);
  background-position: 50% 0%; 
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: 100%;
  --webkit-transition: background-position ease .35s;
  -o-transition: background-position ease .35s;
  -webkit-transition: background-position ease .35s;
  transition: background-position ease .35s;
  /*overflow-y: hidden;*/
}

.--content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  min-height: 100vh;
  max-width: 420px;
  height: 100%;
  width: 100%;
  margin: auto;
}

.--content>img {
  width: 100%;
}

#root {
  position: relative;
  top: 0;
}

main {
  position: relative;
  top: 0;
  min-height: 100%;
  padding: 0 0 5rem !important;
}

.--hidden {
  /*display: none;*/
  opacity: 0;
}

/* get rid of some default style with semantic ui */
main.ui.grid {
  margin-top: 0; 
}

div.--banner {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  overflow: hidden;
  height: 70vh;
  width: 100%;
  max-width: 1080px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes Gradient {
  0% {
    background-position: 50% 0%
  }
  50% {
    background-position: 50% 100%
  }
  100% {
    background-position: 50% 0%
  }
}

@keyframes Gradient {
  0% {
    background-position: 50% 0%
  }
  50% {
    background-position: 50% 100%
  }
  100% {
    background-position: 50% 0%
  }
}


