.--flex.--wrapper {
  position: relative;
  display: -ms-flexbox !important;
  display: flex !important;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  min-width: 100%;
  height: 100%;
}

.--flex.--wrapper::before {
	display: inline-block;
	content: "";
	border-top: 5px solid white;
	border-bottom: 5px solid white;
}

.--background {
	/*position: absolute;*/
	/*left: 25%;*/
	width: 100%;
}

.--lines {
	position: relative;
	top: .5rem;
	margin-top: .5rem;
	/*height: 3rem;*/
	/*transform: rotate(-45deg);*/
}

.visible.transition {
	width: 100%;
	height: 100%;
}

@media only screen and (max-width: 897px) {
  .--flex.--wrapper {
    min-width: auto;
    width: 100% !important;
  }
}


